<template>
    <section>

        <div class="form--group mt-1">
            <div class="custom-control p-0 custom-radio mr-4 custom-control-inline">
                <input type="radio" name="IsDocs" class="m-2" value="0" v-model="Case"
                    v-on:change="ScannedRadiotype('RFID')" :value="0" />Rfid Tags
            </div>
            <div class="custom-contol p-0 custom-radio mr-4 custom-control-inline">
                <input type="radio" class="m-2" name="IsDocs" value="1" v-on:change="ScannedRadiotype('Type')" />Beacon
            </div>
        </div>
        <loader :is-visible="isLoading"></loader>
        <tg-list :showCheckBox="true" :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset"
            IdentityColumn="Id" HeaderText="Scanned Items" :ListData="GroupData" :HeaderData="Headers" :widgets="widgets"
            :callbackfunction="GetScannedList" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="GetScannedList" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata"
            :NorecordfoundText="$t('NoRecordfound')">

        </tg-list>
        <ScannedItemPopup v-if="IsAssignGroup" :checkboxarray="checkboxarray" @CloseScannedItem="CloseScannedItem"
            :assDetailIDs="assDetailIDs">
        </ScannedItemPopup>
    </section>
</template>
<script>
import DataService from '../../service/DataService';
import Schema from '../Asset/ScanneditemsSchema'
import ScannedItemPopup from '../../components/Modal/ScannedItemPopup.vue';
import Pager from '../../components/Common/Pager.vue';


export default {
    components: {
        Pager,
        ScannedItemPopup
    },
    data() {
        return {
            txtsearch: null,
            Case: 0,
            ListType1: true,
            BeaconType: true,
            EPC_TAG: '',
            Popup: false,
            ListType2: false,
            sortBy: '',
            sortExp: '',
            Name: '',
            Type: null,
            GroupData: [],
            noRecord: false,
            TotalRecords: 0,
            pageNum: 1,
            PageSize: 10,
            CurrentPage: 1,
            TotalPages: 0,
            isLoading: false,
            selectAll: false,
            selectedItems: 0,
            IsAssignGroup: false,
            assDetailIDs: '',
            id: '',
            widgets: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            checkboxarray: [],
            UnAssigned: false,
            is_unassigned:1,
            Headers: [{
                "COLUMN_NAME": 'EPC_TAG',
                "DATA_TYPE": "image",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Name",
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": 'Name',
                "DATA_TYPE": "image",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "UserName",
                "DISPLAY_ORDER": 2,

            },
            {
                "COLUMN_NAME": 'email_id',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Email",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": 'group_name',
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Group Name ",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": 'ReadTime',
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Reading Time ",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": 'unique_name',
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Attached Items  ",
                "DISPLAY_ORDER": 3
            },



            ]
            //isLoading:false

        }
    },
    created: function () {

        this.GetScannedList();



    },
    methods: {

        AddToArray($event) {

            console.log('even', $event)
            if ($event.currentTarget.checked) {
                this.checkboxarray.push($event.currentTarget.value);
            }
            else {
                this.checkboxarray.pop();
            }
        },
        actionButtonClick: function (callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case "Consume":
                    vm.Consume(item);
                    break;
                case "RequestQuantity":
                    vm.RequestQuantity(item);
                    break;
            }
        },
        renderActions(row) {
            //  debugger
            var self = this;

        },
        ScannedRadiotype(IsActive) {

            if (IsActive == 'RFID') {
                // this.ListType1 = false;
                //  this.ListType2 = true;
                this.Type = 'RFID';
                this.GetScannedList();

            } else {
                //  this.ListType2 = false;
                //  this.ListType1 = true;
                this.Type = 'Beacon';
                this.GetScannedList();
            }

        },





        GetScannedList: async function () {

            var vueObj = this;
            this.isLoading = true;
            var parms = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.pageNum}&sortBy=${this.sortBy}&sortExp=${this.sortExp}&Type=${this.Type}&${vueObj.searchCondition}`;
            setTimeout(() => {
                this.isLoading = false;
            }, 1000);
           await DataService.GetScannedItemsList(parms).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0) {

                        vueObj.GroupData = response.data;

                        vueObj.TotalRecords = response.data[0].TOTAL
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.pageNum;
                        vueObj.noRecord = false;


                    }
                    else {
                        vueObj.GroupData = [];
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0;

                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.GroupData = [];
                    vueObj.TotalRecords = 0
                    vueObj.isLoading = false;
                }


                setTimeout(function () { vueObj.CheckBoxBootstrap() }, 100);

            });
        },
        leftCommonSearch: function (value) {
            debugger
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson
                    ;
            }
            else {
                this.searchCondition = "";
            }
            this.GetScannedList();
        },
        SearchData: function () {
            // this.pageNum=1;         
            this.GetScannedList();
        },
        commonHeaderButtonClick(event) {

            switch (event.callbackfunction) {
                case "Assigngroup":
                    this.Asign();
                    break;

                case "UnAssigngroup":
                    this.IsAssignedgroup();
                    break;
                case "DeleteRFID":
                    this.DeleteRFIDs();
                    break;
            }

        },
        ResetData: function () {
            this.EPC_TAG = '';
            this.GetScannedList()
        },
        pagerMethod: function (value) {

            this.PageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            // this.TotalRecords=value.TotalRecords;
            this.GetScannedList();
        }
        ,
        sortdata: function (s) {
            debugger
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.GetScannedList();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        }
        ,

        
        AssigngroupPopup: function () {
            this.IsAssignGroup = true;
        },
        CloseScannedItem: function () {
            this.IsAssignGroup = false;
        },
        UnAssigngroupPopup: function () {
            this.UNIsAssignGroup = true;
        },
        

        Asign: function () {
            debugger
            var vm = this;
            var val = "";
            var temp = [];
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                debugger
                for (let i = 0; i < vm.GroupData.length; i++) {
                    if (vm.GroupData[i].Id == $(this).val()) {

                        debugger
                        temp.push(vm.GroupData[i]);
                        console.log('new', vm.assDetailIDs)
                    }
                }
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });

            if (val == "") {
                vm.confirmR(vm.$t('Minimum One EPC Tag should be selected.'), true, this.$t('GetScannedList'), function (result) {
                    return false;
                });
                return false;
            }
            vm.assDetailIDs = val;
            vm.IsAssignGroup = true;

        },


        
         IsAssignedgroup: function () {
            debugger
            var vm = this;
            var val = "";
            var temp = [];
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                debugger
                for (let i = 0; i < vm.GroupData.length; i++) {
                    if (vm.GroupData[i].Id == $(this).val()) {

                        debugger
                        temp.push(vm.GroupData[i]);
                        console.log('new', vm.assDetailIDs)
                    }
                }
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });

            if (val == "") {
                vm.confirmR(vm.$t('Minimum One EPC Tag should be selected.'), true, this.$t('GetScannedList'), function (result) {
                    return false;
                });
            }
           
            else {

                vm.confirmR(vm.$t('Are you sure you want to unassign selected Tag(s)?'), true, this.$t('GetScannedList'), function (result) {
                    // let josobj = {
                    //     groupNames: '',
                    //     selectedRFIDS: vm.assDetailIDs,
                    //     is_unassigned: nullgroupNames, string selectedRFIDS,  int is_unassigned
                    // }
                    var url=`groupNames=&selectedRFIDS=${val}&is_unassigned=1`
                    var obj = JSON.stringify(url);
                    DataService.AddSaveAssignGroup(url).then(response => {

                        if (response.data == 'True') {
                            vm.GetScannedList();
                        }
                    });
                });
                return false;

            }

            vm.assDetailIDs = val;
            vm.UNIsAssignGroup = true;
        },

        DeleteRFIDs:function () {

            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });

            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Id")), function () {
                    var postJSON = {
                        ids: val,

                    };

                    var poststing = JSON.stringify(postJSON);
                    DataService.deleteRFID(poststing).then(response => {

                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('EPC_TAG ')]), "Success", true, vm.$t('Alert'));
                            vm.GetGroupList();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Id')]), "failure", true, vm.$t('Alert'));
                        }
                        //window.location.reload();
                    });
                });
            }
        },

    }
}
</script>